body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: 'Roboto', sans-serif;
  text-align: center;
  padding: 20px;
  background-color:#1c2427;
  min-height: 100vh;
}

h2 {
  margin-bottom: 40px;
}

.btn-primary {
  background-color:#3ab54a;
  border-color:#009345 ;
  font-family: 'Roboto', sans-serif;
}

.btn-primary:hover {
  color: #fff;
  background-color:#009345;
  border-color: #009345;
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show>.btn-primary.dropdown-toggle {
  color: #fff;
  background-color:#009345;
  border-color: #009345;
}

hr { border-color:#353a3e ;}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#main-logo {
  max-width: 200px;
  margin-bottom: 60px;
  margin-top: 40px;
}

.message {
  color: #ffffff;
}


#editprojectformoverlay {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: none;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#editprojectform-container {
  
  display: block;
  width: 500px;
}

/* SEARCH RESULTS */

#search-results-container {display: none;}
.added {
  background-color:#009345 !important;
  border-color: #009345 !important;
  cursor: auto !important;
}

.thumbnail-container {
  max-height: 100px;
  height: 100px;
}

/* SLICK SLIDER */
.slick-slider {
  margin-bottom: 40px;
}

.slick-slide {
  padding: 10px;
}

.favourite-slide {display: inline;}

.favourite-slide h4 {font-size: 1.2rem;}
.favourite-slide p { font-size: 1rem;}


.slick-dots li button:before {
  color:#efefef !important;
}

.slick-dots li.slick-active button:before {
  color:#ffffff !important;
}

